<template>
  <div
    :class="
      typeof weather.main != 'undefined' && weather.main.temp > 16 ? 'warm' : ''
    "
    class="home"
  >
    <main>
      <div class="search-box">
        <input
          type="text"
          class="search-bar"
          placeholder="Search..."
          v-model="query"
          @keypress.enter="fetchWeather"
        />
      </div>
      <Cities @get-city="getCity($event)" :cities="cities" />
      <button @click="getPositionWeather">取得當前地區天氣</button>
      <div class="weather-wrap" v-if="typeof weather.main != 'undefined'">
        <div class="location-box">
          <div class="location">
            {{ weather.name }}, {{ weather.sys.country }}
          </div>
          <div class="date">{{ dateBuilder() }}</div>
        </div>
        <div class="weather-box">
          <div class="temp">{{ Math.round(weather.main.temp) }}°c</div>
          <div class="weather">{{ weather.weather[0].main }}</div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
// @ is an alias to /src
import Cities from '../components/Cities.vue'
export default {
  name: 'Home',
  components: {
    Cities,
  },
  data() {
    return {
      api_key: 'c2f1dd42276e57bdfa93f97f1ef46583',
      url_base: 'https://api.openweathermap.org/data/2.5/',
      query: '',
      weather: {},
      lat: '',
      lon: '',
      cities: [
        'Taipei',
        'New Taipei',
        'Keelung',
        'Taoyuan',
        'Hsinchu',
        'Miaoli',
        'Taichung',
        'Changhua',
        'Nantou',
        'Yunlin',
        'Chiayi',
        'Tainan',
        'Kaohsiung',
        'Pingtung',
        'Yilan',
        'Hualien',
        'Taitung ',
        'Penghu',
        'Green Island',
        'Orchid Island',
        'Kinmen',
        'Matsu',
      ],
    }
  },

  methods: {
    getCity(selected) {
      console.log(selected)
      fetch(
        `${this.url_base}weather?q=${selected}&units=metric&APPID=${this.api_key}`
        //  `${this.url_base}weather?lat=${this.lat}&lon=${this.lon}&APPID=${this.api_key}`
      )
        .then((res) => {
          return res.json()
        })
        .then(this.setResults)
    },
    getPositionWeather() {
      console.log(this.lat, this.lon)

      fetch(
        //`${this.url_base}weather?q=${this.query}&units=metric&APPID=${this.api_key}`
        `${this.url_base}weather?lat=${this.lat}&lon=${this.lon}&APPID=${this.api_key}&units=metric`
      )
        .then((res) => {
          return res.json()
        })
        .then(this.setResults)
    },
    fetchWeather(e) {
      console.log(this)
      fetch(
        `${this.url_base}weather?q=${this.query}&units=metric&APPID=${this.api_key}`
        //  `${this.url_base}weather?lat=${this.lat}&lon=${this.lon}&APPID=${this.api_key}`
      )
        .then((res) => {
          return res.json()
        })
        .then(this.setResults)
    },
    setResults(results) {
      this.weather = results
    },
    dateBuilder() {
      let d = new Date()
      let months = [
        'January',
        'February',
        'March',
        'April',
        'May',
        'June',
        'July',
        'August',
        'September',
        'October',
        'November',
        'December',
      ]
      let days = [
        'Sunday',
        'Monday',
        'Tuesday',
        'Wednesday',
        'Thursday',
        'Friday',
        'Saturday',
      ]

      let day = days[d.getDay()]
      let date = d.getDate()
      let month = months[d.getMonth()]
      let year = d.getFullYear()

      return `${day} ${date} ${month} ${year}`
    },
  },
  created() {
    //do we support geolocation
    if (!('geolocation' in navigator)) {
      this.errorStr = 'Geolocation is not available.'
      return
    }

    this.gettingLocation = true
    // get position
    navigator.geolocation.getCurrentPosition(
      (position) => {
        this.gettingLocation = false
        this.lat = position.coords.latitude.toFixed(4)
        this.lon = position.coords.longitude.toFixed(4)
      },
      (err) => {
        this.gettingLocation = false
        this.errorStr = err.message
        alert(error.code + ": " + error.message);
      },
      { timeout: 10000 }
    )
  },
}
</script>

